<template>
  <div class="operation-behave-map-page">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/soc-attack-event' }">运营中心</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/soc-attack-event' }">攻击事件</el-breadcrumb-item>
        <el-breadcrumb-item>查看行为轨迹 </el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <div class="header-container">
          <div class="query-time-container">
            <el-select v-model="valueTime" placeholder="Select" v-show="isShowSelectTime" @change="onChangeSelectTime">
              <el-option v-for="item in optionTime" :key="item.value" :label="item.label" :value="item.value" />
            </el-select>
            <div v-show="!isShowSelectTime">
              <el-date-picker v-model="pickerTime" type="datetimerange" range-separator="-" start-placeholder="开始时间"
                end-placeholder="结束时间" @change="changeTimeline" />
            </div>
            <el-button icon="el-icon-search" @click="onChangeSearch" class="search-icon-btn" />
          </div>
          <a class="el-button el-button--primary is-plain" href="/#/soc-attack-event">返回</a>
        </div>


        <div v-if="mapAttackerEntity.length == 0">
          <el-empty description="NO DATA" />
        </div>
        <div>
          <el-timeline class="timeline-box">
            <el-timeline-item v-for="(item, index) in mapAttackerEntity" :key="index" :timestamp="item.StartAttackTime"
              placement="top" size="large" color="#409eff">
              <div class="operation-behave-dialog-box">
                <div class="operation-behave-item" style="color:#409eff;padding: 7px 15px;">
                  <div class="operation-behave-content" style="font-size: 18px;">
                    <span>{{ item.URL }}</span>
                  </div>
                </div>
                <el-card shadow="hover" style="margin-left: 15px;">
                  <div class="operation-behave-item">
                    <span class="operation-behave-label">攻击类型</span>
                    <div class="operation-behave-content" v-for="(i, j) in item.AttackTypes" :key="j">
                      {{ i }}
                    </div>
                  </div>
                  <div class="operation-behave-item" v-if="logConfig == 'true'">
                    <span class="operation-behave-label">开始请求时间</span>
                    <div class="operation-behave-content">
                      {{ item.FirstRequestTime }}
                    </div>
                  </div>
                  <div class="operation-behave-item">
                    <span class="operation-behave-label">开始攻击时间</span>
                    <div class="operation-behave-content">
                      {{ item.StartAttackTime }}
                    </div>
                  </div>
                  <div class="operation-behave-item">
                    <span class="operation-behave-label">最新攻击时间</span>
                    <div class="operation-behave-content">
                      {{ item.LatestAttackTime }}
                    </div>
                  </div>
                  <div class="operation-behave-item" v-if="logConfig == 'true'">
                    <span class="operation-behave-label">请求次数</span>
                    <div class="operation-behave-content">
                      {{ item.TotalRequestCount }}
                    </div>
                  </div>
                  <div class="operation-behave-item">
                    <span class="operation-behave-label">攻击次数</span>
                    <div class="operation-behave-content">
                      {{ item.AttackCount }}
                    </div>
                  </div>
                  <div class="operation-behave-item">
                    <span class="operation-behave-label">拦截次数</span>
                    <div class="operation-behave-content">
                      {{ item.BlockCount }}
                    </div>
                  </div>
                  <div class="operation-behave-item">
                    <span class="operation-behave-label"></span>
                    <div class="operation-behave-content button">
                      <el-button @click="handleLook(item)" type="text">查看详情</el-button>
                      <!-- <a class="el-button el-button--text" :href="'/#/soc-query-log/'+ uuid" target="_blank">查看详情</a> -->
                    </div>
                  </div>
                </el-card>
              </div>
            </el-timeline-item>
          </el-timeline>

        </div>
        <el-row type="flex" class="margin-border" justify="space-between">
          <el-col :span="12"></el-col>
          <el-col :span="12" class="text-align-right">
            <a class="el-button el-button--primary is-plain" href="/#/soc-attack-event">返回</a>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { mixin, JXAjax, formatterDateTime } from "../assets/scripts/common";
import { useRoute } from 'vue-router'
export default {
  mixins: [mixin],
  data() {
    return {
      loadingPage: false,
      loading: false,
      mapAttackerEntity: [],
      uuid: '',
      isShowSelectTime: true,
      valueTime: "1w",
      pickerTime: [],
      optionTime: [
        { value: '1h', label: '1小时' },
        { value: '1d', label: '24小时' },
        { value: '1w', label: '7天' },
        { value: '1m', label: '30天' },
        { value: 'default', label: '自定义' }
      ],
      logConfig: "false"
    };
  },
  mounted() {
    var t = this;
    const route = useRoute()
    t.uuid = route.params.uuid;
    t.getLogConf();
    
  },
  methods: {
    getData() {
      var t = this;
      var getUrl = "/soc/soc_attack_event_get_behave_track";
      if (t.logConfig == "true") { 
        getUrl = "/soc/soc_attack_event_get_all_log_behave_track";
      }
      t.loadingPage = true;
      JXAjax(
        "post",
        getUrl,
        {
          from_time: formatterDateTime(t.pickerTime[0]),
          to_time: formatterDateTime(t.pickerTime[1]),
          attack_ip: t.uuid
        },
        function (response) {
          t.loadingPage = false;
          t.mapAttackerEntity = response.data.data;
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    getLogConf() {
      var t = this;
      var getUrl = "/waf/waf_get_sys_log_conf";
      JXAjax(
        "post",
        getUrl,
        {},
        function (response) {
          t.loadingPage = false;
          t.logConfig = response.data.message.log_all;
          t.onChangeSearch();
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    handleLook(item) {
      var t = this;
      var time = {};
      time.type = t.valueTime;
      time.from_time = formatterDateTime(t.pickerTime[0]);
      time.to_time = formatterDateTime(t.pickerTime[1]);
      window.open("/#/soc-query-log/" + this.uuid + '/' + encodeURIComponent(item.Host) + '/' + encodeURIComponent(item.URI) + '/' + JSON.stringify(time));
    },
    onChangeSearch() {
      this.onChangeSelectTime()
      this.getData()
    },
    onChangeSelectTime() {
      var t = this;
      if (t.valueTime == 'default') {
        t.isShowSelectTime = false;
        t.pickerTime = [
          new Date(new Date().getTime() - 24 * 60 * 60 * 1000),
          new Date()
        ];
      } else {
        t.isShowSelectTime = true;
        if (t.valueTime == '1h') {
          t.pickerTime = [
            new Date(new Date().getTime() - 1 * 60 * 60 * 1000),
            new Date()
          ];
        }
        if (t.valueTime == '1d') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000),
            new Date()
          ];
        }
        if (t.valueTime == '1w') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * 7),
            new Date()
          ];
        }
        if (t.valueTime == '1m') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * 30),
            new Date()
          ];
        }
      }
    },
    changeTimeline(event) {
      var t = this;
      if (event == null) {
        t.isShowSelectTime = true;
        t.valueTime = '1w'
      } else {
        t.isShowSelectTime = false;
      }
    },
  },
};
</script>
<style>

.operation-behave-map-page .operation-behave-label {
  width: 120px;
  display: inline-block;
  text-align: right;
  padding: 0 20px 0 0;
  box-sizing: border-box;
}
.operation-behave-dialog-box p {
  display: inline-block;
}
.operation-behave-item{
  display: flex;
  padding: 10px 0;
}
.operation-behave-content{
  -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    position: relative;
    font-size: 14px;
    white-space: normal;
    word-break: break-all;
    word-wrap: break-word;
    
}
.operation-behave-content.button button:first-child{
  margin-right: 20px;
}
.operation-behave-map-page .query-time-container {
  display: block;
  float: left;
}
.query-time-container .el-button {
  margin-left: 10px;
}
.operation-behave-map-page .el-timeline-item__content {
  width: 100%;
}

.operation-behave-map-page .timeline-box .el-timeline-item__timestamp {
  position: absolute;
  left: -150px;
}

.operation-behave-map-page .el-timeline-item {
  margin-left: 150px;
}

.operation-behave-map-page .el-timeline-item__wrapper {
  padding-left: 18px;
}
.timeline-box .el-timeline-item__wrapper {
  display: flex;
}

.timeline-box .el-timeline-item__content {
  position: relative;
  top: -8px;
}

.timeline-box .el-timeline-item__timestamp.is-top {
  font-size: 14px;
  color: #000;
}

.operation-behave-map-page .header-container {
  display: block;
  text-align: right;
  padding-bottom: 15px;
  margin-bottom: 15px;
  border-bottom: 1px solid #ebeef5;
}
</style>
